
.alert_success, .alert_error {
    padding: 15px;
    position: absolute;
    top: 20px;
    right: 0;
    background: green;
    width: 300px;
    color: #fff;
    z-index: 10000;
    transition: all 1s;
}
.alert_error {
    background: red;
}
p {
    margin: 0;
}



